import {useState,useEffect} from 'react';
import useFetch from './useFetch';
import jsonfiledata from '../data/dataType';



function useDataType() {

  /** useFetch hook
   *  if hosting is localhost it will fetch local file json data 
   *  if remote it fetch json data from dev.yellow-website.com/yw-d9 site */
  const {data:types, loading:typeLoading, error:typeError} = useFetch("https://dev.yellow-website.com/yw-d9/jsonapi/taxonomy_term/demo_data_type", jsonfiledata);

  //console.log("Types",types);

  const [dataType, setDataType] = useState([]);

  useEffect(()=>{
    const arr = [];
    types?.data?.length > 0 &&
    types?.data?.map((item) => {
        return arr.push(item.attributes.name)
    })
    setDataType(arr);
  },[types.data])

  return {dataType, typeLoading, typeError}

}

export default useDataType